import React, { useState, useEffect } from "react";
import Title from "../Typography/Title";
import { styled } from "@mui/material/styles";

import { Box, Container } from "@mui/material";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";

const DropDownList = styled("ul")({
    width: "100%",
});

const DropDownItem = styled("li")((props) => ({
    width: "100%",
    borderBottom: props.isactive ? "none" : "1px solid #c4c4c4",
}));

const AnswerField = styled("div")(({ theme }) => ({
    width: "100%",
    backgroundColor: theme.custom.primary.color3,
}));

function Question() {
    const [data, setData] = useState();

    useEffect(() => {
        setData(DUMMY_DATA);
    }, []);

    const openQuestionHandler = (itm) => {
        const newData = data.map((q) =>
            itm.id === q.id ? { ...q, isOpen: !q.isOpen } : q
        );
        setData(newData);
    };

    return (
        <Container>
            <Title></Title>
            <DropDownList>
                {data?.map((itm, idx) => (
                    // isActive  ?  Boolean  +  String ?
                    <DropDownItem key={idx} isactive={+itm.isOpen}>
                        <Box
                            sx={{
                                display: "flex",
                                justifyContent: "space-between",
                                alignItems: "center",
                            }}
                        >
                            <h4>{`Q${idx + 1}. ${itm.question} `}</h4>
                            <ArrowForwardIosIcon
                                //  SVG  style
                                style={{
                                    transition: ".5s",
                                    transform: itm.isOpen
                                        ? "rotate(90deg)"
                                        : "rotate(0)",
                                }}
                                onClick={() => openQuestionHandler(itm)}
                                sx={{ cursor: "pointer" }}
                            />
                        </Box>
                        <AnswerField
                            className={
                                itm.isOpen ? "active-answer" : "close-answer"
                            }
                        >
                            {itm.answer}
                        </AnswerField>
                    </DropDownItem>
                ))}
            </DropDownList>
        </Container>
    );
}

export default Question;

const DUMMY_DATA = [
    {
        id: 1,
        question: "?",
        answer: ",LINEIG;",
        isOpen: false,
    },
    {
        id: 2,
        question: "?",
        answer: "",
        isOpen: false,
    },
    {
        id: 3,
        question: "??",
        answer: "",
        isOpen: false,
    },
    {
        id: 4,
        question: "?",
        answer: "",
        isOpen: false,
    },
];
